<template>
<div>
  <!-- <v-data-table :headers="headers" :items="desserts" :search="search" sort-by="calories" class="elevation-1"> -->
    <template>
      <v-toolbar flat color="white">
        <v-text-field
        v-model="search"
        label="Search"
        single-line
        hide-details
        ></v-text-field>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">buat makanan</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <v-layout class="mx-4">
      <v-flex>
        <template>
          <v-row>
            <v-col cols="3" v-for="makanan in desserts" :key="makanan.id">
              <v-card class="mx-auto" max-width="344" hover>
                <v-card-text>
                  <div>MAKANAN</div>
                  <p class="display-1 text--primary">
                    {{makanan.name}}
                  </p>
                  <p>Calories : {{makanan.calories}}</p>
                  <p>Fat : {{makanan.fat}}</p>
                  <p>Carbs : {{makanan.carbs}}</p>
                  <p>Protein : {{makanan.protein}}</p>
                </v-card-text>
                <v-card-actions>
                  <v-btn text color="deep-purple accent-4" @click="edititem(makanan)">
                    EDIT
                  </v-btn>
                  <v-btn text color="deep-purple accent-4" @click="deleteitem(makanan)">
                    Delete
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
          
          
        </template>
      </v-flex>
    </v-layout>
    <!-- <template v-slot:item.action="{ item }">
      <v-icon class="mr-1" @click="edititem(item)">mdi-silverware</v-icon>
      <v-icon class="mr-1" @click="deleteitem(item)">mdi-delete</v-icon>
    </template> -->
  <!-- </v-data-table> -->
</div>
</template>

<script>

import { dessertsRef } from '../firebase';

  export default {
    data: () => ({
      cek: -1,
      search: '',
      dialog: false,
      headers: [{
          text: 'Dessert (100g serving)',
          align: 'left',
          sortable: false,
          value: 'name',
        },
        {
          text: 'Calories',
          value: 'calories'
        },
        {
          text: 'Fat (g)',
          value: 'fat'
        },
        {
          text: 'Carbs (g)',
          value: 'carbs'
        },
        {
          text: 'Protein (g)',
          value: 'protein'
        },
        {
          text: 'Actions',
          value: 'action',
          sortable: false
        },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),
    firebase: {
      desserts : dessertsRef
    },
    computed: {
      formTitle() {
        return this.cek === -1 ? 'New Item' : 'Edit Item'
      },
    },
    watch: {
      dialog(val) {
        val || this.close()
      },
    },
    methods: {
      close() {
        this.dialog = false
      },
      save() {
        // methods untuk save
        if (this.cek > -1) {
          dessertsRef.child(this.editedIndex).set({name: this.editedItem.name,calories: this.editedItem.calories,
          fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
          this.clear()
        } else {
          dessertsRef.push({name: this.editedItem.name, calories: this.editedItem.calories,
          fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
          this.clear()
        }
        this.close()
      },
      edititem(item) {
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteitem(item) {
        confirm('Are you sure you want to delete this item?') && dessertsRef.child((item['.key'])).remove()
      },
      clear(){
        this.editedItem={}
        this.editedItem.name=''
        this.editedItem.calories=0
        this.editedItem.fat=0
        this.editedItem.carbs=0
        this.editedItem.protein=0
        this.cek=-1
      }
    },
  } 
</script>